import React from 'react';
const Card = ({id,name,email}) => {    
    let url = `https://robohash.org/test${id}`;
    if(id===96){
        url= `data:image/jpeg;base64,`;
    }
    if(id===69){
        url=`https://scontent.fdel1-3.fna.fbcdn.net/v/t1.0-9/38720860_1491879850958310_1582299559965491200_n.jpg?_nc_cat=103&_nc_ht=scontent.fdel1-3.fna&oh=29d3ca1aacb7dc7bbc48471d8ea00f9f&oe=5C8217EB`;
    }
    return(
        <div className='tc grow bg-light-green dib ma2 br4 pa3 bw2 shadow-5'>
            <img src={url} height='300px' width='300px' alt='robot'/>
            <div>
                <h3>{name}</h3>
                <p>{email}</p>
            </div>
        </div>
    )
    
}

export default Card;